.App {
  height: 100%; 
  max-width: 1680px; 
  margin: 0 auto; 
  text-align: center;
  font-family: 'Kalam', cursive;
  background-color: #CD853F; 
}

/*--------------------nav bar------------------------*/
.logo {
  width: 55px; 
  height: 40px; 
  margin-left: 30px; 
}

nav {
  width: 100%; 
  height: 50px; 
  display: -ms-flexbox; 
  display: flex; 
  -ms-flex-direction: row; 
      flex-direction: row; 
  -ms-flex-align: center; 
      align-items: center;  
  -ms-flex-pack: justify;  
      justify-content: space-between; 
  background-color: #FFE4C4;
}

nav p {
  font-size: 18px; 
  font-weight: 700; 
  color: #8B4513;  
  margin-right: 60px; 
}

/*----------------burger builder--------------------*/
.burger-builder {
  background-color: white; 
  height: 330px; 
  overflow: scroll;  
  padding-bottom: 20px; 
}

.layer {
  margin: 4px auto 0 auto; 
}

.top {
  margin-top: 30px; 
  width: 400px; 
  height: 80px; 
  border-radius: 50% 50% 0 0; 
  -webkit-box-shadow: inset -15px 0 #c14822; 
          box-shadow: inset -15px 0 #c14822;
  background: -webkit-linear-gradient(55deg,  #ff7920ce, #ff792096, #ff7920);
  background: -o-linear-gradient(55deg,  #ff7920ce, #ff792096, #ff7920);
  background: linear-gradient(35deg,  #ff7920ce, #ff792096, #ff7920);
  position: relative; 

}

.seed1 {
  width: 20px; 
  height: 40px;
  position: absolute; 
  background-color: white;
  left: 20%;
  top: 30%;
  border-radius: 40%;
  -webkit-transform: rotate(-20deg);
      -ms-transform: rotate(-20deg);
          transform: rotate(-20deg);
  -webkit-box-shadow: inset -2px -3px #c9c9c9;
          box-shadow: inset -2px -3px #c9c9c9; 
}

.seed2 {
  width: 20px; 
  height: 40px;
  position: absolute; 
  background-color: white;
  left: 47%;
  top: 20%;
  border-radius: 40%;
  -webkit-transform: rotate(30deg);
      -ms-transform: rotate(30deg);
          transform: rotate(30deg);
  -webkit-box-shadow: inset -3px 0 #c9c9c9;
          box-shadow: inset -3px 0 #c9c9c9; 
}

.seed3 {
  width: 20px; 
  height: 40px;
  position: absolute; 
  background-color: white;
  left: 79%;
  top: 40%;
  border-radius: 40%;
  -webkit-transform: rotate(-70deg);
      -ms-transform: rotate(-70deg);
          transform: rotate(-70deg);
  -webkit-box-shadow: inset -3px 0 #c9c9c9;
          box-shadow: inset -3px 0 #c9c9c9; 
}

.salad {
  width: 430px; 
  height: 23px; 
  border-radius: 10px; 
  background: -webkit-gradient(linear, left top, left bottom, from(#23bc1e), to(#97e5a3)); 
  background: -webkit-linear-gradient(#23bc1e, #97e5a3); 
  background: -o-linear-gradient(#23bc1e, #97e5a3); 
  background: linear-gradient(#23bc1e, #97e5a3);
}

.bacon {
  width: 400px; 
  height: 9px; 
  background-color: rgba(153, 43, 43, 0.85); 
}

.cheese {
  width: 450px; 
  height: 15px; 
  border-radius: 10px; 
  background: -webkit-gradient(linear, left top, left bottom, from(#e7da1e), to(#e2d409)); 
  background: -webkit-linear-gradient(#e7da1e, #e2d409); 
  background: -o-linear-gradient(#e7da1e, #e2d409); 
  background: linear-gradient(#e7da1e, #e2d409);
}

.meat {
  width: 400px; 
  height: 25px; 
  border-radius: 10px; 
  background: rgb(94, 41, 41); 
}

.bottom {
  width: 400px; 
  height: 55px; 
  border-radius: 0 0 35px 35px; 
  background: -webkit-linear-gradient(55deg,  #ff7920ce, #ff792096, #ff7920); 
  background: -o-linear-gradient(55deg,  #ff7920ce, #ff792096, #ff7920); 
  background: linear-gradient(35deg,  #ff7920ce, #ff792096, #ff7920);
}

/*----------------ingredient selection---------------------*/
h3 {
  font-size: 27px; 
  margin: 10px; 
  color: white; 
}

.price-num {
  width: 75px; 
}

.ingredient {
  display: -ms-flexbox;
  display: flex; 
  -ms-flex-direction: row; 
      flex-direction: row; 
  -ms-flex-pack: center; 
      justify-content: center; 
  line-height: 43px; 
}

i {
  display: inline-block; 
  cursor: pointer; 
  font-size: 36px; 
  margin: 0;
}

span {
  display: inline-block;
  font-size: 35px; 
}

.ingredient-name {
  margin: 0; 
  width: 150px; 
  font-size: 20px; 
  text-align: left; 
}

.number {
  width: 40px; 
}
/*------------------button section---------------------*/
.buttons {
  margin: 20px 0 40px 0; 
}

button {
  cursor: pointer; 
  border: 2px solid white; 
  border-radius: 5px; 
  background-color: #8B4513; 
  font-size: 18px; 
  font-family: 'Kalam', cursive;
  color: white; 
  padding: 5px 25px; 
  margin: 0 40px; 
}

/*-------------------footer----------------------------*/
footer {
  background-color: #DEB887; 
  padding: 45px 0; 
  font-family: 'Merienda', cursive;
}

footer p {
  margin: 5px 0; 
}

footer p:nth-child(2) {
  font-weight: 800; 
}

footer a {
  text-decoration: none; 
  color: rgb(94, 41, 41); 
}

footer a:hover {
  color: rgba(94, 41, 41, 0.8); 
}

/*------------------@media-----------------------------*/
@media(max-width: 550px) {
  .top {
    width: 75vw; 
    border-radius: 50% 50% 0 0; 
  }
  
  .salad {
    width: 80vw; 
  }
  
  .bacon {
    width: 75vw; 
  }
  
  .cheese {
    width: 85vw; 
  }
  
  .meat {
    width: 75vw; 
  }
  
  .bottom {
    width: 75vw; 
  }

  button {
    margin: 2px 15px; 
  }
}

@media(min-height: 880px) {
  nav {
    height: 80px; 
  }
  .burger-builder {
    height: 500px; 
  }
  .top {
    margin-top: 80px; 
  }
  h3 {
    margin: 60px auto 40px auto; 
  }
  .buttons {
    margin: 40px 0 60px 0; 
  }
}

@media(min-height: 1150px) {
  .burger-builder {
    height: 600px; 
  }
  footer {
    padding: 105px 0; 
  }
}

@media(min-width: 1679px) {
  .logo {
    width: 65px; 
    height: 50px; 
    margin-left: 50px; 
  }
  nav p {
    font-size: 28px; 
  }
  h3 {
    font-size: 40px; 
  }
  .price-num {
    font-size: 45px; 
  }
  .ingredient-name {
    width: 250px; 
    font-size: 40px; 
    padding: 10px 0 10px 0; 
  }
  .number {
    width: 80px; 
    font-size: 44px; 
    padding: 10px 0 10px 0; 
  }
  i {
    font-size: 52px; 
  }
  button {
    font-size: 28px; 
  }
  footer {
    font-size: 25px; 
  }
}


